import { BaseComponent, ComponentProps, NodeDefinition } from "../../BaseComponent";
import { createFrame, createText, createVector } from "../../ComponentHelpers";
import { checkbox } from "../checkbox/checkbox";

// SVG Assets
import SVG_Chevron from "./assets/SubSection_assets_icon_Vector_I1174_3092_5698_23_svg_12x7_400000095367432.svg";

export interface ExpandedCardItem {
  name: string;
  isSelected?: boolean;
  isHovered?: boolean;
}

export interface ExpandedCardProps extends ComponentProps {
  headerTitle?: string;
  badgeLabel?: string;
  items?: ExpandedCardItem[];
}

export class expanded_card extends BaseComponent {
  async create(props: ExpandedCardProps): Promise<SceneNode> {
    const defaultItems: ExpandedCardItem[] = [
      { name: "Anti-Doping, Medical and Health", isSelected: true, isHovered: false },
      { name: "Communication, PR and Media", isSelected: false, isHovered: true },
      { name: "Event and Volunteer Management", isSelected: false, isHovered: false },
      { name: "Football and Social Responsibility", isSelected: false, isHovered: false },
      { name: "Football Development", isSelected: false, isHovered: false },
      { name: "Governance, Organisation of Football and Sport", isSelected: false, isHovered: false },
      { name: "Innovation", isSelected: false, isHovered: false },
      { name: "Leadership", isSelected: false, isHovered: false },
      { name: "Marketing Sponsorship", isSelected: false, isHovered: false },
      { name: "Legal and Integrity", isSelected: false, isHovered: false }
    ];

    const items = props.items || defaultItems;
    const headerTitle = props.headerTitle || "Subject";
    const badgeLabel = props.badgeLabel || "Subjects";
    const count = items.length;

    // Chunk items into pairs for columns
    const columns: ExpandedCardItem[][] = [];
    for (let i = 0; i < items.length; i += 2) {
      columns.push(items.slice(i, i + 2));
    }

    const structure: NodeDefinition = createFrame("expanded_card", {
      layoutMode: "VERTICAL",
      itemSpacing: 24,
      paddingTop: 24,
      paddingRight: 24,
      paddingBottom: 24,
      paddingLeft: 24,
      primaryAxisSizingMode: "AUTO",
      counterAxisSizingMode: "AUTO",
      fills: [{ type: "SOLID", color: { r: 1, g: 1, b: 1 } }],
      cornerRadius: 8,
      effects: [{
        type: "DROP_SHADOW",
        color: { r: 0, g: 0, b: 0, a: 0.12 },
        offset: { x: 0, y: 2 },
        radius: 6,
        showShadowBehindNode: true
      }]
    }, [
      // Header
      createFrame("Header", {
        layoutMode: "HORIZONTAL",
        itemSpacing: 24,
        primaryAxisSizingMode: "FIXED",
        counterAxisSizingMode: "AUTO",
        layoutAlign: "STRETCH",
        layoutProps: { height: 40 }
      }, [
        createText("Subject", headerTitle, 20, "SemiBold", { r: 0.102, g: 0.192, b: 0.235 }, {
          font: { family: "Open Sans", style: "SemiBold" },
          lineHeight: { unit: "PIXELS", value: 25 },
          layoutGrow: 1
        }),
        // Badge
        createFrame("Badge", {
          layoutMode: "HORIZONTAL",
          itemSpacing: 8,
          paddingTop: 4,
          paddingBottom: 4,
          paddingLeft: 16,
          paddingRight: 16,
          primaryAxisSizingMode: "AUTO",
          counterAxisSizingMode: "AUTO",
          counterAxisAlignItems: "CENTER",
          fills: [{ type: "SOLID", color: { r: 1, g: 1, b: 1 } }],
          strokes: [{ type: "SOLID", color: { r: 0, g: 0.635, b: 0.588 } }],
          cornerRadius: 100
        }, [
          createText("Label", badgeLabel, 14, "Regular", { r: 0.102, g: 0.192, b: 0.235 }, {
            font: { family: "Open Sans", style: "Regular" },
            lineHeight: { unit: "PERCENT", value: 130 }
          }),
          createText("Count", count.toString(), 16, "Bold", { r: 0.102, g: 0.192, b: 0.235 }, {
            font: { family: "Open Sans", style: "Bold" },
            lineHeight: { unit: "PERCENT", value: 130 }
          })
        ]),
        // Expand Button
        createFrame("Expand Button", {
          layoutMode: "HORIZONTAL",
          paddingTop: 8,
          paddingBottom: 8,
          paddingLeft: 8,
          paddingRight: 8,
          fills: [{ type: "SOLID", color: { r: 0.945, g: 0.953, b: 0.973 } }],
          cornerRadius: 100,
          layoutProps: { width: 40, height: 40 }
        }, [
          createFrame("Icon Wrapper", {
            layoutMode: "NONE",
            clipsContent: true,
            layoutProps: { width: 24, height: 24 }
          }, [
            createVector("Vector", SVG_Chevron, {
              fills: [{ type: "SOLID", color: { r: 0.102, g: 0.192, b: 0.235 } }],
              layoutProps: {
                parentIsAutoLayout: false,
                width: 12,
                height: 7.4,
                relativeTransform: [[1, 0, 6], [0, 1, 8]]
              }
            })
          ])
        ])
      ]),
      // Items Container
      createFrame("Items Container", {
        layoutMode: "HORIZONTAL",
        itemSpacing: 32,
        primaryAxisSizingMode: "AUTO",
        counterAxisSizingMode: "AUTO"
      }, columns.map((colItems, index) =>
        createFrame(`Column ${index + 1}`, {
          layoutMode: "VERTICAL",
          itemSpacing: 8,
          primaryAxisSizingMode: "AUTO",
          counterAxisSizingMode: "AUTO"
        }, colItems.map(item => ({
          type: "COMPONENT" as const,
          name: "Item Row",
          component: checkbox,
          props: {
            characterOverride: item.name,
            checked: item.isSelected,
            hugContents: true,
            hoverState: item.isHovered
          },
          layoutProps: {
            parentIsAutoLayout: true,
            height: 24,
            layoutAlign: "INHERIT"
          }
        })))
      ))
    ]);

    const root = await this.renderDefinition(structure);
    root.x = props.x ?? 0;
    root.y = props.y ?? 0;
    return root;
  }
}
